import { Fragment } from "react";
import { Menu, Transition } from "@headlessui/react";
import { ChevronDownIcon } from "@heroicons/react/20/solid";
import { Link } from "@remix-run/react";
import { v4 as uuid } from "uuid";

function classNames(...classes: string[]) {
  return classes.filter(Boolean).join(" ");
}

const lanaguages = [{ lanaguage: "ENGLISH" }, { lanaguage: "CHINEESE" }, { lanaguage: "VIETNAMESE" }];

export default function Example() {
  return (
    <Menu as="div" className="relative inline-block text-left">
      <div>
        <Menu.Button className="inline-flex w-full justify-center gap-x-1.5 rounded-md bg-white px-3 py-2 text-custom-xs font-semibold font-dinround text-color-hare-always-light shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50">
          SITE LANGUAGE ENGLISH
          <ChevronDownIcon className="-mr-1 h-5 w-5 text-gray-400" aria-hidden="true" />
        </Menu.Button>
      </div>

      <Transition as={Fragment} enter="transition ease-out duration-100" enterFrom="transform opacity-0 scale-95" enterTo="transform opacity-100 scale-100" leave="transition ease-in duration-75" leaveFrom="transform opacity-100 scale-100" leaveTo="transform opacity-0 scale-95">
        <Menu.Items className="absolute right-0 z-10 mt-2 w-56 origin-top-right rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
          <div className="py-1">
            {lanaguages.map((item) => {
              return <Menu.Item key={uuid()}>{({ active }) => <div className={classNames(active ? "bg-gray-100 text-gray-900" : "text-gray-700", "block px-4 py-2 text-custom-xs")}>{item.lanaguage}</div>}</Menu.Item>;
            })}
          </div>
        </Menu.Items>
      </Transition>
    </Menu>
  );
}
